import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { EntryPage } from '../pages/entry/entry';
import { RegisterPage } from '../pages/register/register';
import { EventPage } from '../pages/event/event';
import { EventDetailPage } from '../pages/event/eventDetail/eventDetail';
import { ProfilePage } from '../pages/profile/profile';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import { ClubPage } from '../pages/club/club';
import { MyClubsPage } from '../pages/club/myClubs/myClubs';
import { AllClubsPage } from '../pages/club/allClubs/allClubs';
import { ClubTabsPage } from '../pages/club/clubTabs/clubTabs';


import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';


const firebaseAuth = {
    apiKey: "AIzaSyCkJh51QufXF7wEfpuR1_L6g5yUyGBFWew",
    authDomain: "uq-clubhub.firebaseapp.com",
    databaseURL: "https://uq-clubhub.firebaseio.com",
    projectId: "uq-clubhub",
    storageBucket: "uq-clubhub.appspot.com",
    messagingSenderId: "626079250531"
  };


@NgModule({
  declarations: [
    MyApp,
    EventPage,
    ProfilePage,
    ClubPage,
    HomePage,
    TabsPage,
    MyClubsPage,
    AllClubsPage,
    ClubTabsPage,
    EventDetailPage,

    RegisterPage,

    EntryPage,
  ],


  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/register/register.module#RegisterPageModule', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/entry/entry.module#EntryPageModule', name: 'EntryPage', segment: 'entry', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp(firebaseAuth),
    AngularFireDatabaseModule, // imports firebase/database, only needed for database features
    AngularFireAuthModule, // imports firebase/auth, only needed for auth features
  ],


  bootstrap: [IonicApp],

  entryComponents: [
    MyApp,
    EventPage,
    ProfilePage,
    ClubPage,
    HomePage,
    TabsPage,
    MyClubsPage,
    AllClubsPage,
    ClubTabsPage,
    EventDetailPage,

    RegisterPage,
    EntryPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



